.item {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.button {
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  width: 80px;
  margin: 0 10px;
  color: azure;
  text-transform: capitalize;
  background-color: crimson;
  cursor: pointer;
}
.button:hover {
  background-color: rgb(197, 32, 65);
}
